import "./App.css";
import React, { useState, useEffect } from "react";
import Preloader from "../src/components/Pre";
import Navbar from "./components/Navbar";
import Home from "./components/Home/Home";
import About from "./components/About/About";
import Blogs from "./components/Blogs/Blogs";
import Footer from "./components/Footer";
import PageViews from "./components/PageViews/PageViews";
import { BrowserRouter as Router, withRouter, Route, Switch} from "react-router-dom";
import "./style.css";
import "bootstrap/dist/css/bootstrap.min.css";
import ScrollToTop from "./components/ScrollToTop";
import axios from "axios";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import "./pageTransitions/slideTransition.scss";


function App() {

  const getData = async () => {
    axios.get('https://flask-qa-deploy-postgres.herokuapp.com/api/coord')
    const res = await axios.get('https://geolocation-db.com/json/')
    const addr = await axios.get('https://ipinfo.io/'+res.data.IPv4+'?token=360b82d6f0c132')
    var bodyFormData = new FormData();
    console.log(addr);
    bodyFormData.append('ip', res.data.IPv4);
    bodyFormData.append('loc', addr.data.loc);
    bodyFormData.append('city', addr.data.city);
    bodyFormData.append('country', addr.data.country);
    bodyFormData.append('org', addr.data.org);
    bodyFormData.append('postal', addr.data.postal);
    bodyFormData.append('region', addr.data.region);
    bodyFormData.append('timezone', addr.data.timezone);
    bodyFormData.append('time', Date());
    axios.post('https://flask-qa-deploy-postgres.herokuapp.com/api',  bodyFormData).then(respnse => Response.JSON);
    

  }
  
  const getDepth = (route)=>{
    if(route === '/profile') return 0;
    if(route === '/profile/about') return 1;
    if(route === '/profile/blogs') return 2;
    if(route === '/profile/page-views') return 3;
    return 0;
  }

  const getDirection = (location)=>{
    let prevPath = localStorage.getItem('path')
    if(prevPath === null) prevPath =0;
    let prevDepth = getDepth(prevPath)
    localStorage.setItem('path', location.pathname);
    let currentDepth = getDepth(localStorage.getItem('path'))
    let xyz = prevDepth - currentDepth >=0 ? "right" : "left";
    console.log(xyz);
    return xyz;
  }

  const [load, upadateLoad] = useState(true);
  useEffect(() => {
    getData()
    setTimeout(() => {
      upadateLoad(false);
    }, 1200);
  }, []);

  return (
    <Router>
      <Preloader load={load} />
      <div className="App" id={load ? "no-scroll" : "scroll"}>
        <Navbar />
        <ScrollToTop />
        <Route
        render={({ location }) => {
          const currentKey = location.pathname.split("/")[1] || "/";
          //Specify the duration of the animation (on enter and on exit)
          const timeout = { enter: 700, exit: 400 };
          
          return (
            <TransitionGroup  className="App">
              <CSSTransition
                key={currentKey}
                timeout={timeout}
                classNames="pageSlider"
                mountOnEnter={false}
                unmountOnExit={true}
              >
                <div className = {getDirection(location)}>
                  <Switch location={location}>
                    <Route exact path="/profile" component={Home} />
                    {/* <Route exact path="/blogs" component={Blogs} /> */}
                    <Route exact path="/profile/blogs" render={() => (window.location = "https://gourav-dhar.com")} />
                    {/* <Route exact path="/profile/about" component={About} /> */}
                    <Route exact path="/profile/page-views" component={PageViews} /> 
                  </Switch>
                </div>
              </CSSTransition> 
            </TransitionGroup>
          ); 
        }}
      />
        
      </div>
    </Router>
  );
}

export default withRouter(App);
